<script>
// 
import dayjs from 'dayjs';
import Header from '../my-components/header';
// store
import {mapState} from 'pinia'
import storeId from '@/store/index'  //storeId
export default {
  name: 'SewagecontrolUserManagement',

  data() {
    return {
      // 
      keyWord:'',
      dialogVisible:false,
      // 
      username:'',
      phone:'',
      address:'',
      // 
      newUsername:'',
      newPhone:'',
      newAddress:'',
      // 
      time:dayjs(new Date()).format('YYYY-MM-DD')
      }
    },

  mounted() {
    
  },

  methods: {
    // 
    sureSubmit(){
      const store = storeId()
     // 
    if(!this.username||!this.phone||!this.address){
      this.$message.error('');
    }else{
      // 
      function newUserData(name,number,address,time){
        this.name = name
        this.number = number
        this.address = address
        this.date = time
        this.identity = ''
        this.delete_show = false
        this.edit_show = false
        this.admin_show = false
      }
      const newUser = new newUserData(this.username,this.phone,this.address,this.time)
      // console.log(newUser)
      // 
      this.tableData.unshift(newUser)
      const newData = this.tableData
      console.log(newData)
      // store
      store.updateTableData(newData)
        this.username = ''
        this.phone = ''
        this.address = ''
        this.dialogVisible = false
    }

    },
    // 
    sureDelete(props){
      const store = storeId()
      const row = props.row
      // 
      const newData = this.tableData.filter((item)=>item.name!==row.name)
      // store
      store.updateTableData(newData)
      this.$message({
          message: '',
          type: 'success'
        });
    },
    // 
    sureEdit(props){
      const store = storeId()
      const row = props.row
      // 
      if(!this.newUsername&&!this.newAddress&&!this.newPhone){
        this.$message.error('');
      }
      else{
        // 
        const newData = this.tableData.map(item=>{
          if(item.name === row.name){
            item.name = this.newUsername?this.newUsername:item.name
            item.address = this.newAddress?this.newAddress:item.address
            item.number = this.phone?this.newPhone:item.number
          }
          return item
        })
        row.edit_show = false
        store.updateTableData(newData)
        this.newUsername = ''
        this.newAddress = ''
        this.newPhone = ''
        this.$message({
          message: '',
          type: 'success'
        });
      }
    },
    sureEdit(props){
      this.$message({
          message: '',
          type: 'success'
        });
      props.row.edit_show = false
    },
    // 
    changeIdentity(scope){
      const store = storeId()
      const row = scope.row
      console.log(row)
      const newData = this.tableData.map(item=>{
        if(item.name === row.name){
          item.identity = item.identity===''?'':''
        }
       return item
      })
      this.$message({
          message: '',
          type: 'success'
        });
        scope.row.edit_show = false
               // store
       store.updateTableData(newData)

      // store

      // 
      row.admin_show = false
    }
    },
   
  components:{
    Header
  },
  computed:{
    //  -- 
    ...mapState(storeId,['tableData'])
  }
};
</script>
