    sureDelete(props){
      const store = storeId()
      const row = props.row
      // 
      const newData = this.problemsData.filter((item)=>item.id!==row.id)
      // store
      store.updateProblemsData(newData)
      this.$message({
          message: '',
          type: 'success'
        });
      
      

    },
    handelSolve(props){
      const row = JSON.stringify(props.row)
      // console.log(row)
      // 
      // 
    
      this.$router.push({
          path: '/index/problemdetail',
          query:{rowData:row}
        })

    },
    // 
    addProblem(){
      // 
      this.addProblemVisible = true
      console.log("add")
    },
    // 
    sureAdd(){
      // 
