<template>
  <div class="login-enter">
    <el-input v-model="username" placeholder="" class="username" @focus="ChangeShowUsername()"  @input="ChangeShowUsername()"></el-input>
    <Attention :show="showUsername" :content="''"></Attention>
    <el-input v-model="password" placeholder="" show-password class="password" @focus="ChangeShowPassword()" @input="ChangeShowPassword()"></el-input>
    <Attention :show="showPassword" :content="''"></Attention>
    <button class="enter-button" @click="gotoIndex()"></button>
    <!--  --  -->
    <div class="help">
      <div class="forget">
        
      </div>
      <div class="reset">
        
      </div>

    </div>
  </div>
</template>

<script>
// store
import {mapState} from 'pinia'
import storeId from '@/store/index' 
import Attention from '../my-components/attention.vue'
export default {
  name: 'SewagecontrolLoginEnter',

  data() {
    return {
      username:"lisii",
      password:"admin123456",
      showUsername:false,
      showPassword:false
    };
  },

  mounted() {
  },

  methods: {
    ChangeShowUsername(){
      if(this.username==''){
        this.showUsername = true
      }else{
        this.showUsername = false
      }
    },
    ChangeShowPassword(){
      if(this.password==''){
        this.showPassword = true
      }else{
        this.showPassword = false
      }
    },
    gotoIndex(){
      //  -- 
      if(!this.username||!this.password){
        // console.log("")
        // 
        this.$message.error('');
      }
      else{
        // 
        const isExist = this.tableData.filter((item)=>{
          return item.username === this.username
        })
        if(isExist.length === 0){
          this.$message.error('');
        }else{
          // 
          if(this.password!==isExist[0].password)  this.$message.error('');
          else{
            //  -- 
            this.$message.success(`${isExist[0].username}`);
            //  -- 
            this.$router.push('/index')
          }
        }
      }
      

    }
  },
  components:{
    Attention
  },
  computed:{
    //  -- 
    ...mapState(storeId,['tableData'])
  }
};
</script>
<style scoped lang="less">
.login-enter{
  .username,.password,.enter-button{
   margin-top: 40px;
  }
  .enter-button{
    width: 100%;
    outline: none;
    border: none;
    background-color: #528684;
    height: 40px;
    border-radius: 3px;
    font-size: 18px;
    color: #fff;
    &:hover{
      background-color: #4f7d7c;
    }

  }
  .help{
    margin-top: 50px;
    display: flex;
    width: 100%;
    .forget,.reset{
      text-align: center;
      flex: 1;
      color: #528684;
    }
  }
}
</style>