<template>
  <div id="index-home">
    <!--  -->
    <Header :title="''"></Header>
    <div class="indexLine">
      <div class="singleBox" v-for="(item, index) in boxList" :key="index">
        <DataBox
          :icon="item.icon"
          :color="item.color"
          :title="item.title"
          :number="item.number"
          :status="item.status"
        ></DataBox>
      </div>
    </div>
    <div class="firstLine">
      <div class="days">
        <el-calendar v-model="value" class="calendar">
          <template slot="dateCell" slot-scope="{ date, data }">
            <div
              @click="
                dialogVisible = true;
                handleDateClick(date);
              "
            >
              <p :class="data.isSelected ? 'is-selected' : ''">
                {{ data.day.split("-")[2] }}
              </p>
            </div>
          </template>
        </el-calendar>
      </div>
      <div class="todo">
        <!--  -->
        <div class="todoTitle">
          <i class="el-icon-notebook-2" style="font-size: 18px"></i>
          
          <span class="todoIcon">
            <i class="el-icon-arrow-right"></i>
          </span>
        </div>
        <!--  -->
        <div class="todoBody">
          <!--  -->
          <div class="todoShow">
            <div
              class="singleTodo"
              v-for="(item, index) in RealtodoList"
              :key="index"
              @click="gotoWhere(item.url)"
            >
              <i class="el-icon-info" style="color: #528684"></i>
              <!--  -->
              <i
                class="el-icon-star-on"
                style="color: #efd94b; font-size: 18px"
                v-show="item.star"
              ></i>
              {{ item.content }}
              <span class="time">{{ item.time }}</span>
            </div>
          </div>
          <!--  -->
          <div class="pagination-area">
            <div class="todoPages">
              <el-pagination
                class="pagination"
                layout="prev, pager, next"
                :total="20"
                @current-change="changePage"
                :current-page="currentPage"
              >
              </el-pagination>
            </div>
          </div>
        </div>
      </div>
      <div class="openUse">
        <!--  -->
        <div class="toolTitle">
          <i class="el-icon-suitcase" style="font-size: 18px"></i>

          
        </div>
        <!--  -->
        <div class="toolBody">
          <div
            class="singleTool"
            v-for="(item, index) in toolList"
            :key="index"
            @click="gotoWhere(item.url)"
          >
            <!--  -->
            <img
              :src="item.img"
              style="height: 50px; width: 50px"
              class="toolImage"
            />
            <!--  -->
            <div class="toolName">{{ item.name }}</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// 
import Header from "../my-components/header";
import DataBox from "../my-components/databox";
// store
import { mapState } from "pinia";
import storeId from "@/store/index";
export default {
  name: "SewagecontrolIndexHome",

  data() {
    return {
      //
      dialogVisible: false,
      value: new Date(),
      // 
      title: "",
      // 
      boxList: [
        {
          title: "",
          number: "21",
          color: "#0ca5b3",
          icon: "el-icon-question",
          status: 1,
        },
        {
          title: "",
          number: "1",
          color: "#4762d7",
          icon: "el-icon-user-solid",
          status: 0,
        },
        {
          title: "",
          number: "13",
          color: "#0ca5b3",
          icon: "el-icon-message-solid",
          status: 1,
        },
        {
          title: "",
          number: "20%",
          color: "#4762d7",
          icon: "el-icon-s-marketing",
          status: 1,
        },
      ],
      currentPage: 1,
      RealtodoList: "",

      // 
      toolList: [
        {
          name: "",
          img: require("../../assets/user.png"),
          url: "/index/usermanagement",
        },
        {
          name: "",
          img: require("../../assets/.png"),
          url: "/index/dataanalyze",
        },
        {
          name: "",
          img: require("../../assets/problem.png"),
          url: "/index/problemmanagement",
        },
      ],
    };
  },

  mounted() {
    //  -- 
    this.RealtodoList = this.todoData[0].singleTodo;
  },

  methods: {
    handleClose(done) {
      this.$confirm("")
        .then((_) => {
          done();
        })
        .catch((_) => {});
    },

    handleDateClick(date) {
      //
      const formattedDate =
        date.getFullYear() +
        "-" +
        ("0" + (date.getMonth() + 1)).slice(-2) +
        "-" +
        ("0" + date.getDate()).slice(-2);
      // 
      console.log("", formattedDate); //  console.log 
      this.$router.push({ path: `/index/dataanalyze?date=${formattedDate}` });

      //this.$router.push({ path: `/datedetail/${formattedDate}` });
    },
    // 
    changePage(pageNumber) {
      // console.log(pageNumber)
      const pageIndex = pageNumber - 1;
      this.RealtodoList = this.todoData[pageIndex].singleTodo;
    },
    gotoWhere(url) {
      this.$router.push(url);
    },
  },
  components: {
    Header,
    DataBox,
  },
  computed: {
    ...mapState(storeId, ["todoData"]),
  },
};
</script>
